import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { login } from "../../store/actions/userActions";
import loginLoadingGif from "../../assets/icons/loading.gif";

export const Login = (props) => {
  const [userCred, setUserCred] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const loggedUser = useSelector((state) => state.userReducer.user);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleChange = ({ target }) => {
    const field = target.name;
    const value = target.value === "number" ? +target.value : target.value;
    setUserCred({ ...userCred, [field]: value });
  };

  const onLogin = (ev) => {
    setIsLoading(true);
    ev.preventDefault();
    dispatch(login(userCred));
  };
  
  useEffect(() => {
    if (loggedUser) history.push("/");
  }, [loggedUser, history]);

  return !isLoading ? (
    <form onSubmit={onLogin}>
      <h1>Login</h1>
      <label htmlFor="email">Email</label>
      <input
        required
        type="text"
        id="email"
        onChange={handleChange}
        name="email"
        placeholder="Ex.. israeli2021@gmail.com"
      />

      <label htmlFor="password">Password</label>
      <input
        required
        type="password"
        id="password"
        onChange={handleChange}
        name="password"
        placeholder="Ex.. !#@fdsf54%"
      />
      <button>Login</button>
      <p onClick={() => history.push("/signup")}>Register now</p>
    </form>
  ) : (
    <img className="login-loading" src={loginLoadingGif} alt="" />
  );
};
